import Vue from 'vue'
import Router from 'vue-router'
import LoginRegister from '@/views/LoginRegister'
import Home from '@/views/Home'

Vue.use(Router)
export default new Router({
  // mode: 'history',
  // mode: 'hash',
  // mode: 'abstract',
  routes: [
    {
      path: '/login_register',
      meta: {
        title: '登录注册'
      },
      name: 'LoginResister',
      component: LoginRegister
    }, {
      path: '/',
      meta: {
        title: '首页'
      },
      name: 'Index',
      component: Home
    }
  ]
})
