//  弹窗集合
.go-window-pop {
  display: none;
  .positionL(fixed, 0, 0, 3);
  right: 0;
  bottom: 0;
  margin: auto;

  &:before {
    display: block;
    content: '';
    .positionL(fixed, 0, 0, 1);
    right: 0;
    bottom: 0;
    margin: auto;
    background: rgba(0, 0, 0, .16);
  }

  .form-tool {
    .positionRB(absolute, 40px, 40px, 1);

    button {
      width: 140px;
      height: 40px;
      background: #fff;
      border: 1px solid @color929292;
      .borderRadius(2px);
      text-align: center;
      font-size: 18px;
      cursor: pointer;
      font-family: open-sans;

      &.cancel {
        color: @color929292;
      }

      &.agree {
        border-color: @color313131;
        margin-left: 22px;
      }
    }
  }

  // 勾选协议弹窗
  .agreement-pop {
    background: #fff;
    width: 500px;
    height: 360px;
    .positionL(fixed, 0, 0, 2);
    right: 0;
    bottom: 0;
    margin: auto;
    padding: 55px 80px 0;
    box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.18);
    font-size: 14px;
    line-height: 20px;

    .thanks-title {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  // 完善弹窗
  .enemyBullet-pop {
    width: 1024px;
    height: 730px;
    background: #fff;
    .positionL(fixed, 0, 0, 2);
    right: 0;
    bottom: 0;
    margin: auto;
    cursor: default;
    .clearF;

    .enemy-l {
