            margin-left: 0;
          }

          &.active {
            border-color: @colorE02D2D;
            color: @colorE02D2D;
          }
        }

        .nav-info {
          width: 160px;
          display: table-cell;
          vertical-align: middle;
          height: 70px;
          font-size: 14px;
          text-align: center;

          .icon-gongsi {
            font-size: 38px;
            vertical-align: middle;
          }

          .icon-gerenziliao, .icon-hezuo1 {
            font-size: 32px;
            vertical-align: middle;
          }

          .icon-hezuo1 {

          }
        }
      }

      // 公共下拉
      .public-down {
        .heightAndLineHeight(40px, 40px);
        padding-left: 10px;
        position: relative;
        border: solid 1px #e7e7e7;

        img {
          margin-right: 6px;
        }

        &:after {
          content: "";
          display: block;
          border-left: 5px solid transparent;
          border-right: 5px solid transparent;
          border-top: 6px solid @colord8d8d8;
          .positionR(absolute, 50%, 12px, 1);
          transform: translateY(-50%);
          .widthAndHeight(0, 0);
        }
      }

      .country-title {
        position: relative;
        cursor: pointer;

        .current-country {
          .public-down;
          font-size: 12px;
          background-color: #fff;
        }

        .total-country {
          .positionL(absolute, 39px, 0, 5);
          border: 1px solid #e7e7e7;
          background: #fff;
          width: 100%;
          max-height: 216px;
          overflow-y: scroll;
          min-height: 216px;
          display: none;
        }

        .country-search {

