            width: 99%;
            margin: 4px 0 4px 3px;
            text-indent: 10px;
          }
        }

        .country-item {
          transition: all .3s;
          min-height: 18px;
          line-height: 20px;
          padding-left: 34px;
          font-size: 12px;
          position: relative;
          cursor: pointer;

          .national {
            display: inline-block;
            width: 20px;
            height: 11px;
            //background-image: url("../images/flag-icons.png");
