            .positionL(absolute, 50%, 4px, 1);
            transform: translateY(-50%);
          }

          &:hover {
            background-color: #d0b8b8;
          }
        }

        .shipping-down {
          width: 138px;
        }
      }

      .enemy-item {
        margin-top: 16px;
        font-size: 14px;
        width: 520px;
        .clearF;

        .enemy-item-label {
          margin-bottom: 10px;
        }

        .service-choose {
          cursor: pointer;

