            margin-right: 20px;
            cursor: pointer;

            input {
              cursor: pointer;
              margin-right: 4px;
              vertical-align: middle;
            }
          }
        }

        .enemy-item-l {
          float: left;
        }

        .enemy-item-r {
          float: right;
        }

        .form-item {
          border: 1px solid #e7e7e7;
          height: 40px;
          .clearF;

          &.active {
            border: 1px solid #66afe9 !important;
            -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
          }

          input {
            width: 100%;
            height: 38px;
            text-indent: 20px;
            display: block;
          }
        }
      }

      .hr {
        border: 0;
        background: #e7e7e7;
        height: 1px;
        margin-top: 20px;
        width: 520px;
      }

      .skype-box {
        border: 0;

        .skype, .social {
          border: 1px solid #e7e7e7;

          &.active {
            border: 1px solid #66afe9 !important;
            -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
          }

          input {
            height: 40px;
            line-height: 40px;
            display: inline-block !important;
            outline: none;
            background-color: #ffffff;
            width: 200px;
            text-indent: 0;
            float: right;
            text-align: left;
          }
        }
      }

      .skype {
        width: 250px;
        text-align: center;
        line-height: 40px;

        span {
          font-size: 22px;
          vertical-align: middle;
          color: #00aff0;
        }
      }

      .social {
        width: 250px;
        line-height: 40px;
        text-align: center;

        span {
          font-size: 22px;
          vertical-align: middle;
          color: #4fac32;
        }

      }

      .quantity-select {
        height: 40px;
        background: #fff;
      }

      .shipping-title {
        width: 250px;
        cursor: pointer;
        position: relative;

        .current-shipping {
          .public-down;
        }

        .total-shipping {
          .positionL(absolute, 39px, 0, 5);
          background: #fff;
          width: 100%;
          border: 1px solid #e7e7e7;
          display: none;
          max-height: 154px;
          overflow-y: scroll;

          .item {
            transition: all .3s;
            .heightAndLineHeight(36px, 36px);
            padding-left: 15px;
            text-align: left;
            box-sizing: border-box;

            &:hover {
              background: #d0b8b8;
            }
          }
        }
      }

      .enemy-tool {
        margin-top: 36px;
        text-align: right;
        padding-right: 20px;

        button {
          .widthAndHeight(160px, 40px);
          font-size: 18px;
          cursor: pointer;

          &.skip {
            width: auto;
            padding: 0 10px;
            color: @color929292;
            background: #fff;
            text-decoration: underline;
            margin-right: 60px;
          }

          &.submit {
            background: @colorE02D2D;
            .borderRadius(2px);
            color: #fff;
